import merge from "lodash-es/merge.js";

import { createStore } from "redux";

import { NODE_ENV } from "../environmentVariables.js";

import { IS_PHONE } from "../utils/browserUtils.js";

import "../utils/constants.js";

import { withoutEmptyStarters } from "../utils/homeScreenUtils.js";

import { getBotName } from "../utils/miscUtils.js";

import { mergeCSSVariables } from "../utils/styleUtils.js";

import { reducers } from "./reducers.js";

import { DEFAULT_THEME_STATE, VIEW_STATE_MAIN_WINDOW_OPEN, VIEW_STATE_LAUNCHER_OPEN, DEFAULT_LAUNCHER, DEFAULT_PERSISTED_TO_BROWSER, DEFAULT_INPUT_STATE, DEFAULT_MESSAGE_STATE, VIEW_STATE_ALL_CLOSED, DEFAULT_TOUR_STATE, DEFAULT_MESSAGE_PANEL_STATE, DEFAULT_CUSTOM_PANEL_STATE, DEFAULT_CITATION_PANEL_STATE, DEFAULT_IFRAME_PANEL_STATE, DEFAULT_AGENT_STATE, DEFAULT_LAYOUT_STATE } from "./reducerUtils.js";

import "../../../types/instance/apiTypes.js";

import enLanguagePack from "../../languages/en.json.js";

import { CornersType } from "../../../types/config/CornersType.js";

