class ServiceManager {
  constructor() {
    this.userDefinedElementRegistry = new Map;
    this.restartCount = 0;
  }
  async fire(busEvent) {
    return this.eventBus.fire(busEvent, this.instance);
  }
}

export { ServiceManager };
