async function destroyWebChat(managedWebChat, setInstance) {
  if (managedWebChat) {
    if (managedWebChat.instance) {
      managedWebChat.instance.destroy();
      await sleep(0);
    }
    managedWebChat.shouldDestroy = true;
    managedWebChat.instance = null;
  }
  setInstance(null);
  await sleep(0);
}

function addUserDefinedResponseHandler(webChatInstance, setUserDefinedResponseEventsBySlot) {
  console.log({
    webChatInstance: webChatInstance
  });
  function userDefinedResponseHandler(event) {
    setUserDefinedResponseEventsBySlot(userDefinedEventsBySlot => ({
      ...userDefinedEventsBySlot,
      [event.data.slot]: {
        fullMessage: event.data.fullMessage,
        messageItem: event.data.message,
        element: event.data.element
      }
    }));
  }
  function userDefinedChunkHandler(event) {
    if ("complete_item" in event.data.chunk) {
      const messageItem = event.data.chunk.complete_item;
      setUserDefinedResponseEventsBySlot(userDefinedEventsBySlot => ({
        ...userDefinedEventsBySlot,
        [event.data.slot]: {
          messageItem: messageItem,
          element: event.data.element
        }
      }));
    } else if ("partial_item" in event.data.chunk) {
      const itemChunk = event.data.chunk.partial_item;
      setUserDefinedResponseEventsBySlot(userDefinedEventsBySlot => ({
        ...userDefinedEventsBySlot,
        [event.data.slot]: {
          partialItems: [ ...userDefinedEventsBySlot[event.data.slot]?.partialItems || [], itemChunk ],
          element: event.data.element
        }
      }));
    }
  }
  function restartHandler() {
    setUserDefinedResponseEventsBySlot({});
  }
  webChatInstance.on({
    type: BusEventType.CHUNK_USER_DEFINED_RESPONSE,
    handler: userDefinedChunkHandler
  });
  webChatInstance.on({
    type: BusEventType.USER_DEFINED_RESPONSE,
    handler: userDefinedResponseHandler
  });
  webChatInstance.on({
    type: BusEventType.RESTART_CONVERSATION,
    handler: restartHandler
  });
}

async function loadChat({managedWebChatRef: managedWebChatRef, managedWebChat: managedWebChat, render: render, setInstance: setInstance, onBeforeRender: onBeforeRender, onAfterRender: onAfterRender, setUserDefinedResponseEventsBySlot: setUserDefinedResponseEventsBySlot, element: element}) {
  await destroyWebChat(managedWebChatRef.current, setInstance);
  managedWebChatRef.current = managedWebChat;
  if (managedWebChat.shouldDestroy) {
    await destroyWebChat(managedWebChat, setInstance);
    return;
  }
  const widget = await instantiateWidget(managedWebChat.config, () => Promise.resolve(Chat), Promise.resolve(render), element);
  const instance = await widget.start();
  addUserDefinedResponseHandler(instance, setUserDefinedResponseEventsBySlot);
  onBeforeRender?.(instance);
  await instance.render();
  onAfterRender?.(instance);
  setInstance(instance);
  managedWebChat.instance = instance;
  if (managedWebChat.shouldDestroy) {
    await destroyWebChat(managedWebChat, setInstance);
  }
}

const AppContainerExport = React.memo(AppContainer);

const loadBaseStyles = async () => {
  const {default: styles} = await import("../../shared/styles/export.scss.js");
  return styles;
};

const loadCarbon = async () => {
  const {default: carbon} = await import("../../shared/styles/export.carbon.scss.js");
  return carbon;
};

async function loadStyles() {
  const [styles, carbonStyles] = await Promise.all([ loadBaseStyles(), loadCarbon() ]);
  return carbonStyles + styles;
}

