function getLauncherButtonAriaLabel(languagePack, isLauncherHidden, isTourActive) {
  if (isTourActive) {
    return isLauncherHidden ? languagePack.launcher_isTourOpen : languagePack.launcher_isTourClosed;
  }
  return isLauncherHidden ? languagePack.launcher_isOpen : languagePack.launcher_isClosed;
}

export { getLauncherButtonAriaLabel };
