function addClassNameToState(name) {
  return previousState => {
    const currentIndex = previousState.extraClassNames.indexOf(name);
    if (currentIndex === -1) {
      return {
        extraClassNames: [ ...previousState.extraClassNames, name ]
      };
    }
    return null;
  };
}

function removeClassNameFromState(name) {
  return previousState => ({
    extraClassNames: previousState.extraClassNames.filter(value => value !== name)
  });
}

export { addClassNameToState, removeClassNameFromState };
