let innerStorage = {};

const storage = {
  getItem(key) {
    return innerStorage[key];
  },
  setItem(key, value) {
    innerStorage[key] = value;
  },
  removeItem(key) {
    delete innerStorage[key];
  },
  length: Object.keys(innerStorage).length,
  clear() {
    innerStorage = {};
  },
  key(index) {
    return Object.keys(innerStorage)[index];
  }
};

export { storage as default };
