import React, { useRef, useCallback } from "react";

import { useSelector } from "react-redux";

import { useEffectDidUpdate } from "../../hooks/useEffectDidUpdate.js";

import { useServiceManager } from "../../hooks/useServiceManager.js";

import "../../../../types/state/AppState.js";

import { IS_PHONE } from "../../utils/browserUtils.js";

import { LauncherDesktopContainer } from "./LauncherDesktopContainer.js";

import { LauncherMobileContainer } from "./LauncherMobileContainer.js";

import { ViewChangeReason, MainWindowOpenReason } from "../../../../types/events/eventBusTypes.js";

import { ViewType } from "../../../../types/instance/apiTypes.js";

