const MS_BETWEEN_RETRIES = [ 1e3, 3e3, 5e3 ];

const MS_MAX_ATTEMPT = 150 * 1e3;

const MS_MAX_SILENT_ERROR = 6e3;

const MS_MAX_SILENT_LOADING = 4e3;

var RetryType;

(function(RetryType) {
  RetryType[RetryType["SILENT"] = 1] = "SILENT";
  RetryType[RetryType["VISIBLE"] = 2] = "VISIBLE";
})(RetryType || (RetryType = {}));

class MessageService {
  constructor(serviceManager, publicConfig) {
    this.pendingLocale = false;
    this.localeIsExplicit = false;
    this.serviceManager = serviceManager;
    this.messageLoadingManager = new MessageLoadingManager;
    this.queue = {
      waiting: [],
      current: null
    };
    const timeoutOverride = publicConfig.messaging?.messageTimeoutSecs;
    this.timeoutMS = timeoutOverride ? timeoutOverride * 1e3 : MS_MAX_ATTEMPT;
  }
  async processSuccess(current, received) {
    const {requestOptions: requestOptions, isProcessed: isProcessed} = current;
    const isWelcomeNode = Boolean(current.message.history.is_welcome_request);
    if (isProcessed) {
      return;
    }
    this.setMessageErrorState(current, MessageErrorState.NONE);
    const {message: message} = current;
    if (received) {
      if (message.input.message_type !== MessageInputType.EVENT) {
        if (!isWelcomeNode) {
          this.messageLoadingManager.end();
        }
        received.history = received.history || {};
        received.history.timestamp = received.history.timestamp || Date.now();
        current.trackData.lastRequestTime = Date.now() - current.timeLastRequest;
        current.trackData.totalRequestTime = Date.now() - current.timeFirstRequest;
        await this.serviceManager.actions.receive(received, isWelcomeNode, message, requestOptions);
      }
      this.messageLoadingManager.end();
    }
    if (current.isProcessed) {
      return;
    }
    {
      current.sendMessagePromise.doResolve();
      current.isProcessed = true;
    }
    this.moveToNextQueueItem();
  }
  addErrorMessage() {
    const {store: store} = this.serviceManager;
    const errorMessage = store.getState().languagePack.errors_singleMessage;
    const {originalMessage: originalMessage, localMessage: localMessage} = createLocalMessageForInlineError(errorMessage);
    store.dispatch(actions.addLocalMessageItem(localMessage, originalMessage, true));
  }
  async resendMessage(message, localMessageID) {
    await this.send(cloneDeep(message), MessageSendSource.HYDRATE_RESEND, localMessageID, {
      skipQueue: true,
      silent: true
    });
  }
  sendToAssistantAndUpdateErrorState(current) {
    if (current.isProcessed) {
      return;
    }
    this.sendToAssistant(current);
    const now = Date.now();
    const msSinceStarted = now - current.timeFirstRequest;
    const isSilentErrorWindow = MS_MAX_SILENT_ERROR > msSinceStarted;
    const type = isSilentErrorWindow ? RetryType.SILENT : RetryType.VISIBLE;
    if (type === RetryType.VISIBLE) {
      this.setMessageErrorState(current, MessageErrorState.RETRYING);
      this.queue.waiting.forEach(waitingMessage => {
        this.setMessageErrorState(waitingMessage, MessageErrorState.WAITING);
      });
    }
  }
  async processError(pendingRequest, resultText, allowRetry) {
    const {message: message, timeFirstRequest: timeFirstRequest, timeLastRequest: timeLastRequest, tryCount: tryCount, isProcessed: isProcessed, trackData: trackData, requestOptions: requestOptions} = pendingRequest;
    const isWelcome = message.history.is_welcome_request;
    const now = Date.now();
    const msSinceStarted = now - timeFirstRequest;
    const isInAttemptWindow = this.timeoutMS > msSinceStarted && tryCount < MS_BETWEEN_RETRIES.length;
    if (isProcessed) {
      return;
    }
    trackData.lastRequestTime = Date.now() - timeLastRequest;
    trackData.totalRequestTime = Date.now() - timeFirstRequest;
    if (isInAttemptWindow && allowRetry) {
      trackData.numErrors++;
      const retryDelay = MS_BETWEEN_RETRIES[pendingRequest.tryCount++];
      setTimeout(() => {
        this.sendToAssistantAndUpdateErrorState(pendingRequest);
      }, retryDelay);
    } else {
      let catastrophicErrorType = false;
      if (isWelcome) {
        catastrophicErrorType = true;
      } else if (requestOptions.silent) {
        this.addErrorMessage();
      }
      this.serviceManager.actions.errorOccurred({
        errorType: OnErrorType.MESSAGE_COMMUNICATION,
        message: "An error occurred sending a message",
        otherData: resultText,
        catastrophicErrorType: catastrophicErrorType
      });
      this.rejectFinalErrorOnMessage(pendingRequest, resultText);
    }
  }
  rejectFinalErrorOnMessage(pendingRequest, resultText = "An undefined error occurred trying to send your message.") {
    const {sendMessagePromise: sendMessagePromise} = pendingRequest;
    this.setMessageErrorState(pendingRequest, MessageErrorState.FAILED);
    const {message: message} = pendingRequest;
    if (pendingRequest === this.queue.current && message.input.message_type !== MessageInputType.EVENT && !message.history.is_welcome_request) {
      this.messageLoadingManager.end();
    }
    sendMessagePromise.doReject(new Error(resultText));
    pendingRequest.isProcessed = true;
    if (pendingRequest === this.queue.current) {
      this.moveToNextQueueItem();
    }
  }
  async sendToAssistant(current) {
    const {store: store} = this.serviceManager;
    const state = store.getState();
    const {customSendMessage: customSendMessage} = state.config.public.messaging;
    current.timeLastRequest = Date.now();
    if (current.isProcessed) {
      return;
    }
    try {
      const message = cloneDeep(current.message);
      current.message = message;
      store.dispatch(actions.updateMessage(message));
      const controller = new AbortController;
      current.sendMessageController = controller;
      debugLog("Called customSendMessage", message);
      await customSendMessage(message, {
        signal: controller.signal
      }, this.serviceManager.instance);
      await this.processSuccess(current, null);
    } catch (error) {
      consoleError("An error occurred while sending a message", error);
      const resultText = error && (typeof error === "string" ? error : JSON.stringify(error)) || "There was an unidentified error.";
      this.processError(current, resultText, !customSendMessage);
    }
  }
  async runQueueIfReady() {
    if (!this.queue.current && this.queue.waiting.length > 0) {
      const {eventBus: eventBus, store: store} = this.serviceManager;
      this.clearCurrentQueueItem();
      this.queue.current = this.queue.waiting.shift();
      const {current: current} = this.queue;
      const {message: message, source: source} = current;
      const state = store.getState();
      const {config: config} = store.getState();
      const {public: publicConfig} = config;
      current.timeFirstRequest = Date.now();
      if (message.input.message_type !== MessageInputType.EVENT) {
        const lastResponse = getLastBotResponseWithContext(state);
        if (lastResponse) {
          message.thread_id = THREAD_ID_MAIN;
        }
        if (!message.history.is_welcome_request) {
          const LOADING_INDICATOR_TIMER = !publicConfig.messaging?.messageLoadingIndicatorTimeoutSecs && publicConfig.messaging?.messageLoadingIndicatorTimeoutSecs !== 0 ? MS_MAX_SILENT_LOADING : publicConfig.messaging.messageLoadingIndicatorTimeoutSecs * 1e3;
          this.messageLoadingManager.start(() => {
            this.serviceManager.store.dispatch(actions.addIsLoadingCounter(1));
          }, didExceedMaxLoading => {
            if (didExceedMaxLoading) {
              this.serviceManager.store.dispatch(actions.addIsLoadingCounter(-1));
            }
          }, () => {
            this.cancelMessageRequestByID(message.id, true);
          }, LOADING_INDICATOR_TIMER, this.timeoutMS);
        }
        if (current.isProcessed) {
          return;
        }
        const originalUserText = message.history?.label || message.input.text;
        await eventBus.fire({
          type: BusEventType.PRE_SEND,
          data: message,
          source: source
        }, this.serviceManager.instance);
        if (current.isProcessed) {
          return;
        }
        const localMessage = inputItemToLocalItem(message, originalUserText, current.localMessageID);
        if (!message.history.silent) {
          store.dispatch(actions.updateLocalMessageItem(localMessage));
          store.dispatch(actions.updateMessage(message));
        }
        deepFreeze(message);
        await eventBus.fire({
          type: BusEventType.SEND,
          data: message,
          source: source
        }, this.serviceManager.instance);
      }
      this.sendToAssistant(current);
    }
  }
  addToMessageQueue(message, source, localMessageID, sendMessagePromise, requestOptions = {}) {
    const newPendingMessage = {
      localMessageID: localMessageID,
      message: message,
      sendMessagePromise: sendMessagePromise,
      requestOptions: requestOptions || {},
      timeFirstRequest: 0,
      timeLastRequest: 0,
      trackData: {
        numErrors: 0,
        lastRequestTime: 0,
        totalRequestTime: 0
      },
      tryCount: 0,
      isProcessed: false,
      source: source
    };
    this.queue.waiting.push(newPendingMessage);
    if (this.queue.current) {
      if (message.history?.error_state === MessageErrorState.RETRYING) {
        this.setMessageErrorState(newPendingMessage, MessageErrorState.WAITING);
      }
    }
  }
  clearCurrentQueueItem() {
    if (this.queue.current) {
      this.queue.current = null;
    }
  }
  moveToNextQueueItem() {
    this.clearCurrentQueueItem();
    this.runQueueIfReady();
  }
  setMessageErrorState(pendingRequest, errorState) {
    const {message: message} = pendingRequest;
    const {allMessagesByID: allMessagesByID} = this.serviceManager.store.getState();
    const messageToUpdate = allMessagesByID[message.id];
    if (messageToUpdate) {
      const currentState = messageToUpdate.history?.error_state;
      const errorSame = currentState === errorState || errorState === MessageErrorState.NONE && !currentState;
      if (!errorSame) {
        let announceMessageID;
        switch (errorState) {
         case MessageErrorState.FAILED:
          {
            announceMessageID = "errors_ariaMessageFailed";
            break;
          }
        }
        if (announceMessageID) {
          this.serviceManager.store.dispatch(actions.announceMessage({
            messageID: announceMessageID
          }));
        }
        this.serviceManager.store.dispatch(actions.setMessageErrorState(message.id, errorState));
        const {allMessagesByID: allMessagesByID} = this.serviceManager.store.getState();
        pendingRequest.message = allMessagesByID[message.id];
      }
    }
  }
  send(message, source, localMessageID, requestOptions) {
    message.history.timestamp = message.history.timestamp || Date.now();
    message.input = message.input || {};
    message.input.message_type = message.input.message_type || MessageInputType.TEXT;
    const sendMessagePromise = resolvablePromise();
    this.addToMessageQueue(message, source, localMessageID, sendMessagePromise, requestOptions);
    this.runQueueIfReady();
    return sendMessagePromise;
  }
  cancelAllMessageRequests() {
    while (this.queue.waiting.length) {
      this.cancelMessageRequestByID(this.queue.waiting[0].message.id, false);
    }
    if (this.queue.current) {
      this.cancelMessageRequestByID(this.queue.current.message.id, false);
      this.clearCurrentQueueItem();
    }
  }
  async cancelMessageRequestByID(messageID, logError) {
    let pendingRequest;
    if (this.queue.current?.message.id === messageID) {
      pendingRequest = this.queue.current;
    } else {
      const index = this.queue.waiting.findIndex(item => item.message.id === messageID);
      if (index !== -1) {
        pendingRequest = this.queue.waiting[index];
        this.queue.waiting.splice(index, 1);
      }
    }
    if (pendingRequest) {
      const {lastResponse: lastResponse, sendMessageController: sendMessageController} = pendingRequest;
      sendMessageController?.abort("Message was cancelled");
      this.rejectFinalErrorOnMessage(pendingRequest, "Message was cancelled");
      if (logError) {
        this.serviceManager.actions.errorOccurred({
          errorType: OnErrorType.MESSAGE_COMMUNICATION,
          message: "Message was cancelled",
          otherData: await safeFetchTextWithTimeout(lastResponse)
        });
      }
    }
  }
}

export { MessageService as default };
