function calcAnnouncementForWidgetOpen(previousState, newViewState) {
  if (isEqual(previousState.persistedToBrowserStorage.launcherState.viewState, newViewState)) {
    return previousState.announceMessage;
  }
  return {
    messageID: newViewState.mainWindow ? "window_ariaWindowOpened" : "window_ariaWindowClosed"
  };
}

function applyBotMessageState(state, newState) {
  return {
    ...state,
    botMessageState: {
      ...state.botMessageState,
      ...newState
    }
  };
}

function handleViewStateChange(state, viewState) {
  let {agentState: agentState} = state;
  let {showUnreadIndicator: showUnreadIndicator} = state.persistedToBrowserStorage.launcherState;
  if (viewState.mainWindow && state.isBrowserPageVisible) {
    if (agentState.numUnreadMessages !== 0) {
      agentState = {
        ...agentState,
        numUnreadMessages: 0
      };
    }
    showUnreadIndicator = false;
  }
  return {
    ...state,
    announceMessage: calcAnnouncementForWidgetOpen(state, viewState),
    agentState: agentState,
    persistedToBrowserStorage: {
      ...state.persistedToBrowserStorage,
      launcherState: {
        ...state.persistedToBrowserStorage.launcherState,
        viewState: viewState,
        showUnreadIndicator: showUnreadIndicator
      }
    }
  };
}

function setHomeScreenOpenState(state, isOpen, showBackToBot) {
  if (showBackToBot === undefined) {
    showBackToBot = state.persistedToBrowserStorage.chatState.homeScreenState.showBackToBot;
  }
  return {
    ...state,
    persistedToBrowserStorage: {
      ...state.persistedToBrowserStorage,
      chatState: {
        ...state.persistedToBrowserStorage.chatState,
        homeScreenState: {
          ...state.persistedToBrowserStorage.chatState.homeScreenState,
          isHomeScreenOpen: isOpen,
          showBackToBot: showBackToBot
        }
      }
    }
  };
}

function applyLocalMessageUIState(state, localMessageID, propertyName, propertyValue) {
  const oldMessage = state.allMessageItemsByID[localMessageID];
  if (oldMessage) {
    return {
      ...state,
      allMessageItemsByID: {
        ...state.allMessageItemsByID,
        [localMessageID]: {
          ...oldMessage,
          ui_state: {
            ...oldMessage.ui_state,
            [propertyName]: propertyValue
          }
        }
      }
    };
  }
  return state;
}

function applyFullMessage(state, message) {
  const newState = {
    ...state,
    allMessagesByID: {
      ...state.allMessagesByID,
      [message.id]: message
    }
  };
  if (!state.allMessagesByID[message.id]) {
    const currentMessageIDs = state.botMessageState.messageIDs;
    const newMessageIDs = [ ...currentMessageIDs, message.id ];
    return applyBotMessageState(newState, {
      messageIDs: newMessageIDs
    });
  }
  return newState;
}

